  display: flex;
  flex-direction: column;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.heading {
  font-weight: 100;
  margin: 2rem;
}

.reset_list {
  list-style: none;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #1a1b15;
  font-size: 2rem;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #1E293B;
  margin-bottom: 5rem;
}

.span {
  font-weight: 600;
}

.boton-verde-block {
  background-color: #00b3b3;
  display: block;
  color: #FFFFFF;
  text-align: center;
  padding: 1.5rem;
  font-size: 2rem;
  font-weight: 700;
}
.boton-verde-block:hover {
  background-color: #009999;
}

.boton-verde-inline {
  all: unset;
  background-color: #00b3b3;
  display: inline-block;
  color: #FFFFFF;
  text-align: center;
  padding: 1.5rem;
  font-size: 2rem;
  font-weight: 700;
}
.boton-verde-inline:hover {
  background-color: #009999;
}

.alerta {
  padding: 1.5rem 1.5rem 1.5rem 3rem;
  margin: 1rem 0;
  text-transform: uppercase;
  font-size: 1.3rem;
  font-weight: 700;
}
.alerta__error {
  background-color: #ffcbcb;
  border-left: 0.5rem solid #cb0000;
  color: #cb0000;
}
.alerta__exito {
  background-color: #bdff9f;
  border-left: 0.5rem solid #329f00;
  color: #329f00;
}

.header__contenedor {
  display: flex;
  flex-direction: column;
  background-color: #00b3b3;
  justify-content: space-between;
  text-align: center;
}
.header__titulo {
  color: #FFFFFF;
  font-size: 4rem;
  font-weight: 500;
  font-weight: 700;
}

@media (min-width: 768px) {
  .header__contenedor {
    display: flex;
    flex-direction: row;
    background-color: #00cccc;
    justify-content: space-between;
    align-items: center;
  }
  .header__titulo {
    padding-left: 4rem;
  }
}
.navegacion {
  display: flex;
  flex-direction: column;
}
.navegacion__enlace {
  all: unset;
  color: #FFFFFF;
  font-size: 1.8rem;
  padding: 2rem;
  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
}
.navegacion__enlace:hover, .navegacion__enlace--actual {
  background-color: #FFFFFF;
  color: #1a1b15;
}

@media (min-width: 1024px) {
  .navegacion {
    flex-direction: row;
    justify-content: space-between;
    margin-right: 4rem;
  }
}
.footer {
  background-color: #00cccc;
  padding-top: 3rem;
  margin-top: 2rem;
}
.footer__titulo {
  color: #FFFFFF;
  font-size: 4rem;
  font-weight: 500;
  font-weight: 700;
}
.footer__contenido {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__contenido {
    align-items: flex-start;
  }
}
.footer__grid {
  max-width: 1200px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
    justify-content: space-between;
  }
}
.footer__texto {
  line-height: 2;
  text-align: center;
  color: white;
}
@media (min-width: 768px) {
  .footer__texto {
    text-align: left;
  }
}
.footer__copyright {
  background-color: #009999;
  margin: 2rem 0 0 0;
  padding: 2rem 0;
  text-align: center;
  font-size: 1.4rem;
  font-weight: 900;
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .footer__copyright {
    margin: 0;
  }
}
.footer__copyright--regular {
  font-weight: 400;
}

.menu-redes {
  display: flex;
  justify-content: center;
  gap: 2rem;
  align-items: center;
  margin: 1rem 0;
}
.menu-redes__enlace::before {
  color: #FFFFFF;
  font-size: 2rem;
  display: inline-block;
  font-style: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  font-family: "Font Awesome 6 Brands";
}
.menu-redes__enlace[href*="facebook.com"]::before {
  content: "\f39e";
}
.menu-redes__enlace[href*="github.com"]::before {
  content: "\f09b";
}
.menu-redes__enlace[href*="tiktok.com"]::before {
  content: "\e07b";
}
.menu-redes__enlace[href*="instagram.com"]::before {
  content: "\f16d";
}
.menu-redes__enlace[href*="youtube.com"]::before {
  content: "\f167";
}
.menu-redes__enlace[href*="twitter.com"]::before {
  content: "\f099";
}
.menu-redes__ocultar {
  display: none;
}
@media (min-width: 768px) {
  .menu-redes {
    justify-content: flex-end;
    margin: 4rem 0;
  }
}

.formulario__fieldset {
  border: none;
  margin-top: 1rem;
  padding: 0;
}
.formulario__legend {
  color: #64748B;
  font-size: 2.2rem;
  margin-bottom: 2rem;
}
.formulario__p {
  color: #64748B;
  margin: 1rem 0;
  font-size: 2rem;
}
.formulario__legend2 {
  color: #64748B;
  font-size: 2.2rem;
}
.formulario__campo {
  display: flex;
  flex-direction: column;
  margin: 1rem 0 1rem 0;
  gap: 1rem;
}
.formulario__label {
  display: block;
  font-size: 1.8rem;
  color: rgba(26, 27, 21, 0.8);
}
.formulario__input, .formulario__input--sociales {
  padding: 1rem;
  border: 1px solid #b1c7dd;
  border-radius: 0.5rem;
}
.formulario__input--file {
  border: none;
  padding-left: 0;
}
.formulario__input--sociales {
  flex: 1;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.formulario__contenedor-icono {
  display: flex;
  border-radius: 0.5rem;
  overflow: hidden;
}
.formulario__icono {
  background-color: #1E293B;
  width: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.formulario__icono i {
  color: #FFFFFF;
  font-size: 2rem;
}
.formulario__texto--imagen {
  margin-top: 2rem;
  color: #1a1b15;
}
.formulario__imagen {
  max-width: 20rem;
}
.formulario__submit {
  background-color: #00b3b3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  width: 100%;
  margin-top: 2rem;
  margin-bottom: 1.5rem;
  align-self: flex-start;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
.formulario__submit:hover {
  background-color: teal;
  cursor: pointer;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
.formulario__submit:hover {
  background-color: #009999;
  cursor: pointer;
}
.formulario__info {
  margin-bottom: 1rem;
}
.formulario__contacto {
  display: flex;
  flex-direction: row;
  gap: 3rem;
  align-items: center;
}
.formulario__div {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  align-items: center;
}

.acciones {
  display: flex;
  justify-content: space-between;
}
.acciones--centrar {
  justify-content: center;
}
.acciones__enlace {
  color: #1a1b15;
  font-size: 1.6rem;
}
.acciones__enlace:hover {
  color: #329f00;
}

.auth {
  width: min(95%, 60rem);
  margin: 0 auto;
  margin-top: 3rem;
  margin-bottom: 3rem;
  border: 1px solid #cccccc;
  padding: 2rem;
}
.auth__heading {
  font-size: 3rem;
  color: #36382b;
}
.auth__texto, .auth__description {
  color: #1a1b15;
  padding-left: 0.5rem;
}
.auth__textoc {
  color: #1a1b15;
}

.dashboard {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #F8FAFC;
}
.dashboard__header {
  background-color: #00b3b3;
  padding: 2rem;
}
.dashboard__heading {
  margin-top: 2rem;
}
.dashboard__header-grid {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .dashboard__header-grid {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.dashboard__logo {
  color: #FFFFFF;
  font-size: clamp(2rem, -0.8rem + 10vw, 3rem);
  text-align: center;
  margin-bottom: 2rem;
  white-space: nowrap;
}
@media (min-width: 1024px) {
  .dashboard__logo {
    text-align: left;
    margin: 0;
  }
}
.dashboard__form {
  display: flex;
  justify-content: center;
}
.dashboard__submit--logout {
  all: unset;
  color: #FFFFFF;
  font-size: 1.4rem;
  text-transform: uppercase;
  font-weight: 700;
  border: none;
  background-color: transparent;
}
.dashboard__submit--logout:hover {
  color: #0da6f3;
  cursor: pointer;
}
.dashboard__submit--logout:hover {
  color: black;
}
.dashboard__grid {
  display: flex;
  flex: 1;
  overflow: auto;
}
.dashboard__sidebar {
  background-color: #00cccc;
  width: 12rem;
}
.dashboard__menu {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.dashboard__enlace {
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2rem;
  transition: 300ms background-color ease-in-out, 300ms color ease, border-radius ease;
}
.dashboard__enlace:hover, .dashboard__enlace--actual {
  background-color: #009999;
  border-radius: 1rem;
}
.dashboard__icono {
  font-size: 2rem;
}
.dashboard__menu--texto {
  font-size: 1.1rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-top: 0.5rem;
}
.dashboard__contenido {
  padding: 1rem 1rem 3rem 1rem;
  width: 100%;
  overflow-y: scroll;
}
@media (min-width: 768px) {
  .dashboard__contenido {
    padding: 1rem 5rem 3rem 5rem;
  }
}
.dashboard__contenedor-boton {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .dashboard__contenedor-boton {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 5rem;
  }
}
.dashboard__boton {
  background-color: #00b3b3;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 100%;
  display: inline-block;
  text-transform: uppercase;
  font-size: 1.4rem;
}
@media (min-width: 768px) {
  .dashboard__boton {
    width: auto;
  }
}
.dashboard__boton:hover {
  background-color: teal;
  cursor: pointer;
}
.dashboard__contenedor, .dashboard__formulario {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
  background-color: #FFFFFF;
  padding: 2rem;
  border-radius: 1rem;
  overflow-x: scroll;
}
@media (min-width: 768px) {
  .dashboard__contenedor, .dashboard__formulario {
    overflow-x: hidden;
  }
}
.dashboard__formulario {
  width: min(95%, 80rem);
  margin: 0 auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
  overflow-y: scroll;
}
.table__thead {
  background-color: #1E293B;
  color: #FFFFFF;
}
.table__th {
  padding: 1rem;
  text-align: left;
}
.table__tr:nth-of-type(even) {
  background-color: #F8FAFC;
}
.table__td, .table__td--acciones {
  padding: 1.5rem 1rem;
}
.table__td--acciones {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  gap: 2rem;
  margin-top: 2rem;
  align-items: center;
}
