.table__accion {
  all: unset;
  text-transform: uppercase;
  font-size: 1.4rem;
  font-weight: 700;
}
.table__accion--editar {
  color: teal;
}
.table__accion--eliminar {
  color: #cb0000;
}
.table__accion:hover {
  cursor: pointer;
}

.paginacion {
  margin: 5rem 0 3rem 0;
